import { useState } from 'react';
import './App.css';
import Character from './Character'
import CreateCharacter from './CreateCharacter'
import DeleteCharacter from './DeleteCharacter'
import {useNuiEvent} from "../hooks/useNuiEvent";
import {fetchNui} from "../utils/fetchNui";

import { TextField } from '@mui/material'

import NumberFormat from 'react-number-format';

interface ReturnData { id: number, dateofbirth: string, gender: string, cash: string, bank: string, phone_number: string, KEK: number }

const App: React.FC = () => {

  const [characters, setCharacters] = useState([])

  const [character, setCharacter] = useState<ReturnData | {id: "", dateofbirth: "", gender: "", cash: "", bank: "", phone_number: "", KEK: 0}>(Object)

  const [showInfo, setshowInfo] = useState(false)
  const [isVisible, setVisible] = useState(false)
  const [CreatingCharacter, setCreatingCharacter] = useState(false)
  const [DeletingCharacter, setDeletingCharacter] = useState(false)

  function selectCharacter(character: any) {
    setCharacter(character)
    if (character.KEK !== 0 || character.KEK) {
      setshowInfo(true)
      fetchNui<ReturnData>('cDataPed', character).then(resData => { })
    } else {
      setshowInfo(false)
    }
  };  

  function ToggleCreating() {
    setCreatingCharacter(false)
    setDeletingCharacter(false)
  }

  useNuiEvent<boolean>('toggleVisiblity', (data: boolean) => {
    setVisible(data)
  })

  useNuiEvent<any>('characterData', (data: any) => {
    setCharacters(data)
  })

  function playCharacter() {
    if (character.KEK === 0 || !character.KEK) {
      setCreatingCharacter(true)
    } else {
      fetchNui<ReturnData>('selectCharacter', character).then(resData => { })
    }
  }

  function deleteCharacter() {
    if (showInfo) {
      setDeletingCharacter(true)
      setCreatingCharacter(false)
    }
  }

  function fullDeleteCharacter() {
    if (showInfo && DeletingCharacter) {
      setDeletingCharacter(false)
      fetchNui<ReturnData>('removeCharacter', character).then(resData => { 
        setshowInfo(false) 
        setCharacter({id: "", dateofbirth: "", gender: "", cash: "", bank: "", phone_number: "", KEK: 0})
      })
    }
  }

  return (
    <div className="app" >
      <CreateCharacter isCreating={CreatingCharacter} tUI={ToggleCreating}  />
      <DeleteCharacter isDeleting={DeletingCharacter} tUI={ToggleCreating} deleteCharacter={fullDeleteCharacter} />
      <div className="main" style={{ display: isVisible && !CreatingCharacter && !DeletingCharacter ? '' : 'none' }}>
        <div className="leftSide">
          <div className="titles">
            <div className="title-1">SELECT</div>
            <div className="title-1 title-2">CHARACTER</div>
          </div>
          <div className="characterGrid">
            {Array.from(Array(8), (e, i) => {
              return <Character value={characters[i] || {id: "Create", firstname: "Character", KEK: 0}} current={character.id} setCharacter={selectCharacter} />
            })}
            
          </div>
          <div className="uiButtons">
            <div className="playButton" onClick={playCharacter}>{(character.KEK === 0 || !character.KEK) ? "Create" : "Play"}</div>
            <div className="deleteButton" onClick={deleteCharacter}>Delete</div>
          </div>
          <div className="uiLinks">
            <div className="uiLink">discord.gg/echorp</div>
            <div className="uiLink">echorp.net</div>
          </div>
        </div>
        
        <div className={`characterInfo ${showInfo ? "displayTrue" : "displayFalse"}`}>
          <div className="characterInfo-title">Character Information</div>
          <hr className="line" />
          <div className="characterInfo-info"><span className="characterInfo-bold">DOB:</span> {character.dateofbirth}</div>
          <div className="characterInfo-info"><span className="characterInfo-bold">Gender:</span> {(character.gender === 'm') ? "Male" : "Female"}</div>
          <div className="characterInfo-info"><span className="characterInfo-bold">Cash:</span> <NumberFormat value={character.cash} displayType={'text'} thousandSeparator={true} prefix={'$'} /></div>
          <div className="characterInfo-info"><span className="characterInfo-bold">Bank:</span> <NumberFormat value={character.bank} displayType={'text'} thousandSeparator={true} prefix={'$'} /></div>
          <div className="characterInfo-info"><span className="characterInfo-bold">Phone Number:</span> <NumberFormat value={character.phone_number} displayType={'text'} format="###-###-####" /></div>
        </div>
      </div>   
    </div>
  );
}

export default App;
