function Character(props: any) {

	function something() {
		if (props.value.KEK === 0) {
			props.setCharacter(props.value)
		} else {
			props.setCharacter(props.value)
		}
	} 

	return (
		<div className={`characterItem ${(props.current === props.value.id) ? "active" : ""}`} onClick={something}>
			<div className="characterId">{props.value.id}</div>
			<div className="characterName">{props.value.firstname} {props.value.lastname}</div>
		</div>
	)
}

export default Character;