import * as React from 'react';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import {fetchNui} from "../utils/fetchNui";
import { createTheme, ThemeProvider } from '@mui/material/styles';

const darkTheme = createTheme({
	palette: {
		mode: 'dark',
	},
});

export default function DeleteCharacter(props: any) {
  const [open, setOpen] = React.useState(false);

  const handleDeleteCharacter = () => {
		props.deleteCharacter()
  };

  const handleClose = () => {
    props.tUI(false)
  };

  return (
    <div>
			<ThemeProvider theme={darkTheme}>
				<Dialog
					open={props.isDeleting}
					onClose={handleClose}
					aria-labelledby="alert-dialog-title"
					aria-describedby="alert-dialog-description"
				>
					<DialogTitle id="alert-dialog-title">
						{"Delete Character?"}
					</DialogTitle>
					<DialogContent>
						<DialogContentText id="alert-dialog-description">Are you sure you want to delete your character? This action in irreversible!</DialogContentText>
					</DialogContent>
					<DialogActions>
						<Button onClick={handleClose}>No</Button>
						<Button onClick={handleDeleteCharacter} autoFocus>Yes</Button>
					</DialogActions>
				</Dialog>
			</ThemeProvider>
    </div>
  );
}