import { Grid, Paper, TextField,FormControl, FormLabel, FormControlLabel, RadioGroup, Radio, Button, IconButton } from '@mui/material';
import 'date-fns'
import React from 'react';
import './App.css';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import { DatePicker } from '@mui/lab';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import {fetchNui} from "../utils/fetchNui";

import CloseIcon from '@mui/icons-material/Close'

import Snackbar, { SnackbarOrigin } from '@mui/material/Snackbar';

const CreateCharacter = (props: any) => {

	const [darkMode, setDarkMode] = React.useState(true);
	const [firstName, setFirstName] = React.useState('')
	const [lastName, setLastName] = React.useState('')
	const [gender, setGender] = React.useState('m')
	const [SnackbarMessage, setSnackbarMessage] = React.useState('')

	const paperStyle = {
		paddingBottom: 10,
		paddingTop: 10,
		paddingLeft: 20,
		paddingRight: 20,
		height: '395px',
		width: 280,
		margin: "auto"
	}

	const [selectedDate, setSelectedDate] = React.useState<Date | null>(null);

	function handleDateChange(date: any) {
		setSelectedDate(date)
	}

	function handlefirstNameChange(name: any) {
		setFirstName(name.target.value)
		console.log(name.target.value)
	}

	function handlelastNameChange(name: any) {
		setLastName(name.target.value)
	}

	function handleGenderChange(gender: any) {
		console.log(gender.target.value)
		setGender(gender.target.value)
	}

	const darkTheme = createTheme({
		palette: {
			mode: darkMode ? 'dark' : 'light',
		},
	});

	const [open, setOpen] = React.useState(false);

	const handleClick = () => {
		if (!firstName || firstName === "") {
			setSnackbarMessage("You must input a first name")
			setOpen(true)
			return
		}

		if (!lastName || lastName === "") {
			setSnackbarMessage("You must input a last name")
			setOpen(true)
			return
		}

		if (!gender || gender === "") {
			setSnackbarMessage("You must select a gender")
			setOpen(true)
			return
		}

		if (!selectedDate) {
			setSnackbarMessage("You must select a date of birth")
			setOpen(true)
			return
		}

		interface ReturnData { firstname: string, lastname: string, gender: string, dob: string }
		
		fetchNui<ReturnData>('createNewCharacter', {
			firstname: firstName,
			lastname: lastName,
			gender: gender,
			dob: new Date(selectedDate).toISOString().split('T')[0]
		}).then(resData => { 
			props.tUI(false)
		})

	}

	function cancelClick() {
		props.tUI(false)
	}

	const handleClose = (
    event: React.SyntheticEvent | React.MouseEvent,
    reason?: string,
  ) => {
		if (reason === 'clickaway') { return }
    setOpen(false);
  };

	return (
		<div className="CUI" style={{ display: props.isCreating ? '' : 'none' }}>

				<Snackbar
					anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
					open={open}
					autoHideDuration={2500}
					onClose={handleClose}
					message={SnackbarMessage}
					action={
						<React.Fragment>
						<IconButton color="primary" onClick={handleClose}>
						<CloseIcon />
						</IconButton>
						</React.Fragment>
					}
				/>

			<ThemeProvider theme={darkTheme}>

				

				<Grid>
				<Paper elevation={5} style={paperStyle}>
					<Grid container direction="column">
						<h2>Create Character</h2>

						<Grid container direction={"column"} rowSpacing={1} >
							<Grid item>
								<TextField onChange={handlefirstNameChange} label="First Name" placeholder='Mihai' required sx={{ width: 1 }}/>
							</Grid>
							<Grid item>
								<TextField onChange={handlelastNameChange} label="Last Name" placeholder='Snow' required sx={{ width: 1 }}/>
							</Grid>

							<Grid item sx={{ width: 1 }}>
										<LocalizationProvider dateAdapter={AdapterDateFns}>
											<div className="customDatePickerWidth">
												<DatePicker 
													label="Date of Birth"
													value={selectedDate}
													onChange={handleDateChange}
													renderInput={(params) => <TextField {...params} />}
												/>
											</div>
										</LocalizationProvider>
							</Grid>

							<Grid item>
								<FormControl component="fieldset">
									<FormLabel component="legend">Gender</FormLabel>
									<RadioGroup row aria-label="gender" name="row-radio-buttons-group">
										<FormControlLabel onChange={handleGenderChange} value="m" control={<Radio />} label="Male" />
										<FormControlLabel onChange={handleGenderChange} value="f" control={<Radio />} label="Female" />
										<FormControlLabel onChange={handleGenderChange} value="other" control={<Radio />} label="Other" />
									</RadioGroup>
								</FormControl>
							</Grid>

							<Grid container direction="row" columnSpacing={1} sx={{ mt: 1 }}>
								<Grid item>
									<Button variant="contained" onClick={handleClick} > Create </Button>
								</Grid>
								<Grid item>
									<Button variant="contained" onClick={cancelClick} > Cancel </Button>
								</Grid>
							</Grid>
							

						</Grid>	

						
					</Grid>
					
				</Paper>
				
			</Grid>
			</ThemeProvider>
			
		</div>
		
	)
}


export default CreateCharacter;